import React from "react";
import { NavBarPC } from "./NavBarPC/NavBarPC";
import { NavBarMobile } from "./NavBarMobile/NavBarMobile";
import { IArrayLinkProps, ILinkProps, IShowMobileNav } from "../.././Interfaces"



export const NavBar: React.FC<IShowMobileNav> = ({ showMobileNav }) => {

    let styleNavBar: string;
    showMobileNav ? styleNavBar = 'link-mobile' : styleNavBar = 'header-link';

    const mainPage: ILinkProps = {
        text: 'Главная',
        path: "/",
        className: styleNavBar
    }

    const categoryPage: ILinkProps = {
        text: 'Категории',
        path: "/cat",
        className: styleNavBar
    }

    const signUp: ILinkProps = {
        text: 'Зарегистрироваться',
        path: "/sign-up",
        className: styleNavBar
    }


    const navProps: IArrayLinkProps = { navProps: [mainPage, categoryPage, signUp], showMobileNav }

    return (
        <>
            {window.screen.width < 767 ? <NavBarMobile {...navProps} /> : <NavBarPC {...navProps} />}
        </>
    )
};